// =========================== >>
// Widget Body Suggestion
// =========================== >>

.suggestion {
  &-list {
    margin: 10px 0px 0px;
    padding: 0;
    list-style-type: none;

    &--item {
      margin: 10px 0px 0px;

      button {
        border: 1px solid #d97109;
        background-color: #d97109;
        border-radius: 5px;
        color: #fff;
        cursor: pointer;
        padding: 0.5rem 0.75rem;

        p {
          margin: 0;
        }
      }
    }
  }
}
